.fren-page {
  &__header-btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    padding: 16px 12px;
    width: 100%;
    margin-top: 23px;
  }
  &__center {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    padding: 112px 12px;
    margin-top: 12px;
  }
  &__center-content {
    display: flex;
    align-items: center;
  }
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    padding: 130px 12px;
    margin-top: 14px;
  }
  &__list-content {
    display: flex;
    align-items: center;
  }
  &__header-btn-content {
    display: flex;
    align-items: center;
  }
}

.black-transparent {
  border-radius: 14px;
  backdrop-filter: blur(14px);
  background: rgba(255, 255, 255, 0.12);

  &_with-icon {
    svg {
      flex-shrink: 0;
      color: rgb(112, 117, 121);
    }
  }
}

._coinIcon {
  // margin-right: 24px;
  width: 50px;
  height: 50px;
  background-image: url("../../assets/images/moneta-small.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-color: transparent;
}

.fren-page {
    &__bear-content {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__bear-icon {
        font-size: 48px;
    }
}