
$background-color: rgba(255, 255, 255, 0.12);
@mixin sp($n: 1) {
  position: absolute;
  z-index: $n;
  border-radius: 12px;
}

.skeleton {
  border-radius: 20px;
  position: relative;
  width: 100%;
  height: 100%;
  background-color: $background-color;
  display: flex;
  align-items: center;
  justify-content: center;
  &__wrapper {
    width: 818px;
    height: 340px;
    position: relative;
  }
}

// circle
.skeleton__circle {
  @include sp();
  border-radius: 100%;
  left: 60px;
  top: 16px;
  width: 48px;
  height: 48px;
  background-color: $background-color;
}

// first line
.element__box1 {
  @include sp();
  left: 120px;
  top: 24px;
  width: 90px;
  height: 8px;
  background-color: $background-color;
}

// second line
.element__box2 {
  @include sp();
  left: 120px;
  top: 50px;
  width: 180px;
  height:8px;
  background-color: $background-color;
}

// little line
.element__box3 {
  @include sp();
  left: 20px;
  top: 34px;
  width: 24px;
  height: 8px;
  background-color: $background-color;
}